.popup__tooltip {
  display: flex;
  flex-direction: column;
  align-items: center;

  row-gap: 32px;
  margin: 0;
  padding: 60px 36px;
  width: 430px;
  background: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  box-sizing: border-box;
}

@media screen and (max-width: 720px) {
  .popup__tooltip {
    padding: 50px 18px;
    column-gap: 40px;
    width: 280px;
  }
}
