.header {
  margin: 0 calc((100% - 880px) / 2);
  padding: 45px 0 0;

  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;

}

@media screen and (max-width: 944px) {
  .header {
    margin-left: 0;
    margin-right: 0;
    padding-left: 27px;
    padding-right: 27px;
    padding-top: 28px;

  }
}
